.author-wrapper {
  position: relative;
  height: 70vh;
  margin-bottom: .15rem;
  background: #fff;
  text-align: center;
}
.author-wrapper img {
  width: 85%;
  height: 100%;
}
.desc {
  padding: .1rem;
  background: #fff;
  margin-bottom: .15rem;
  text-align: center;
}
.desc .title {
  margin-bottom: .1rem;
  font-size: .15rem;
}
.desc .link {
  text-decoration: none;
  color: #b4282d;
}



